import { SET_ASSETS, UPDATE_ASSET } from "./actionTypes";
import { Dispatch, AnyAction } from 'redux'
import { ThunkAction } from 'redux-thunk';
import StateInterface from "../interface"
import client from "../../utils/client";

export const setAssets = (assets: object[]) => ({
  type: SET_ASSETS,
  data: assets
});

export const updateAsset = (asset: object) => ({
  type: UPDATE_ASSET,
  data: asset
});

export const fetchAssets = (query?: string): ThunkAction<Promise<string>, {}, {}> => {
  return (dispatch: Dispatch<AnyAction, StateInterface>) => {
    return new Promise<string>(async (resolve, reject) => {
      try {
        const { data } = await client.get(`/assets?${query}`);
        dispatch(setAssets(data));
        resolve()
      } catch (error) {
        console.error(error);
        dispatch(setAssets([]));
        reject()
      }
    })
  }
}

export function fetchAssetDetails(assetId: string): ThunkAction<Promise<string>, {}, {}> {
  return (dispatch: Dispatch<AnyAction, StateInterface>) => {
    return new Promise<string>(async (resolve, reject) => {
      try {
        const { data } = await client.get(`/asset?id=${assetId}`);
        dispatch(updateAsset(data));
        resolve()
      } catch (error) {
        reject()
      }
    });
  };
}
