import React from "react";
import moment from "moment";
import { Paper } from "@material-ui/core";
import styled from "styled-components";
import { Box, Text } from "../../components/elements";

const TableRow = styled(Paper)`
  padding: 10px;
  border-top: 1px solid lightgrey;
  cursor: pointer;
  :hover {
    background-color: lightgray;
  }
  margin: 10px 0;
`;

const MobileAssetTable = ({ data, history }) => {
  const handleRowClick = name => history.push(`/asset/${name}`);

  return (
    <Box modifiers={["mt30"]}>
      {data.map(asset => (
        <TableRow key={asset.id} onClick={() => handleRowClick(asset.name)}>
          <Box display="flex" justifyContent="space-between">
            <Text modifiers={["m5", "bold"]}>{asset.issuerName}</Text>
            <Text modifiers={["m5", "small"]}>
              {moment(asset.dateCreated).format("MM/DD/YY")}
            </Text>
          </Box>
          <Text modifiers={["m5"]}>{asset.name}</Text>
          <Text modifiers={["m5"]}>{asset.reviewType}</Text>
        </TableRow>
      ))}
    </Box>
  );
};

export default MobileAssetTable;
