import React, { PureComponent } from "react";
import { connect } from "react-redux";
import find from "lodash/find";
import { fetchAssets, fetchAssetDetails } from "../store/actions";

export default function(WrappedComponent) {
  const mapDispatchToProps = dispatch => ({
    fetchAssets: q => dispatch(fetchAssets(q)),
    fetchAssetDetails: id => dispatch(fetchAssetDetails(id))
  });

  const mapStateToProps = (state, props) => ({
    assets: state.assets,
    asset: find(state.assets, asset => asset.name === props.match.params.name),
    isMobile: state.default.isMobile
  });

  class ReduxContainer extends PureComponent {
    componentDidMount() {
      if (!this.props.asset) {
        this.props.fetchAssets();
      } else if (!this.props.asset.detailed) {
        this.props.fetchAssetDetails(this.props.asset.id);
      }
    }

    componentDidUpdate() {
      if (!this.props.asset.detailed) {
        this.props.fetchAssetDetails(this.props.asset.id);
      }
    }

    render() {
      return <WrappedComponent {...this.props} />;
    }
  }
  return connect(mapStateToProps, mapDispatchToProps)(ReduxContainer);
}
