import React, { useState } from "react";
import { connect } from "react-redux";
import { Redirect, useLocation } from "react-router-dom";
import _isEmpty from "lodash/isEmpty";
import client from "../utils/client";
import { Text, Container, Box, Button, Input } from "../components/elements";
import { setUser } from "../store/actions";

const Login = ({ setUser, user, history }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [wrongCreds, setWrongCreds] = useState(false);
  const [internalError, setInternalError] = useState(false);

  let location = useLocation();
  let { from } = location.state || { from: { pathname: "/" } };

  const handleSubmit = async () => {
    setWrongCreds(false);
    try {
      const { data: user } = await client.post("/login", {
        email,
        password
      });
      setUser(user);
      history.push("/dashboard");
    } catch (error) {
      if (!error.response || error.response.status === 500) {
        setInternalError(true);
      } else if (error.response.status === 401) {
        setWrongCreds(true);
      }
    }
  };

  if (!_isEmpty(user)) {
    return <Redirect to={from} />;
  }

  return (
    <Container maxWidth="sm" modifiers={["mt30"]}>
      <Text align="center" variant="h3">
        Moodys Investors Service
      </Text>
      <Box
        display="flex"
        flexDirection="column"
        modifiers={["mt30", "p30", "withBorder", "withBorderRadius"]}
      >
        <Text display="block" variant="h4" align="center">
          Welcome Back!
        </Text>
        {wrongCreds && (
          <Button
            display="block"
            variant="subtitle1"
            align="center"
            color="error"
            variants={["mt10"]}
          >
            Email or Password is incorrect
          </Button>
        )}
        {internalError && (
          <Button
            display="block"
            variant="subtitle1"
            align="center"
            color="error"
            variants={["mt10"]}
          >
            Server Error
          </Button>
        )}
        <form>
          <Box display="flex" flexDirection="column" modifiers={["mt20", "mb20"]}>
            <Input
              id="email-input"
              type="email"
              label="Email *"
              margin="normal"
              variant="outlined"
              value={email}
              onChange={e => setEmail(e.target.value)}
              modifiers={["m5"]}
              autoComplete="email"
            />
            <Input
              id="password-input"
              label="Password"
              type="password"
              autoComplete="current-password"
              margin="normal"
              variant="outlined"
              value={password}
              onChange={e => setPassword(e.target.value)}
              modifiers={["m5"]}
            />
            <Button
              variant="contained"
              onClick={handleSubmit}
              color="primary"
              modifiers={["m5"]}
            >
              Login
            </Button>
          </Box>
        </form>
      </Box>
    </Container>
  );
};

const mapStateToProps = state => ({
  user: state.user
});

const mapDispatchToProps = dispatch => ({
  setUser: user => dispatch(setUser(user))
});

export default connect(mapStateToProps, mapDispatchToProps)(Login);
