import {
  Table as _Table,
  TableBody,
  TableRow,
  TableHead,
  TableCell
} from "@material-ui/core";
import styled, { css } from "styled-components";
import { applyStyleModifiers } from "styled-components-modifiers";
import marginStyles from "../../styles/marginStyles";
import heightAndWithStyles from "../../styles/heightAndWithStyles";

const MODIFIER_CONFIG = {
  noBorder: () =>
    css`
      border: none !important;
      td {
        border: none !important;
      }
    `,
  tightPadding: () =>
    css`
      td {
        padding: 5px;
      }
    `,
  cursor: () =>
    css`
      cursor: pointer !important;
    `,
  ...heightAndWithStyles,
  ...marginStyles
};

export const Table = styled(_Table)`
  ${applyStyleModifiers(MODIFIER_CONFIG)};
`;
export const Tbody = styled(TableBody)`
  ${applyStyleModifiers(MODIFIER_CONFIG)};
`;
export const Th = styled(TableHead)`
  ${applyStyleModifiers(MODIFIER_CONFIG)};
`;
export const Tr = styled(TableRow)`
  ${applyStyleModifiers(MODIFIER_CONFIG)};
`;
export const Td = styled(TableCell)`
  vertical-align: top !important;
  ${applyStyleModifiers(MODIFIER_CONFIG)};
`;
