import React, { useReducer } from "react";
import withAssets from "../../utils/withAssets";
import every from "lodash/every";
import EligibilityItem from "./EligibilityItem";
import PageHeader from "../../components/PageHeader";
import { Container, Text, Box } from "../../components/elements";

const pageData = [
  {
    value: true,
    title: "Data quality",
    description: "Asset has sufficient information to make an assessment"
  },
  {
    value: true,
    title: "Audit Report",
    description:
      "Successful code audit was completed prior to issuance or after code updates"
  },
  {
    value: true,
    title: "Code from approved repository",
    description: "Reusable smart contracts are from an approved repostiory"
  },
  {
    value: true,
    title: "Custody & key management",
    description:
      "A qualified custodian is involed in the digital asset management process"
  },
  {
    value: true,
    title: "Privacy",
    description: "Sensitive information on the platform can be protected"
  },
  {
    value: true,
    title: "Issuance compliance",
    description: "Asset is issued in a compliant manner"
  },
  {
    value: true,
    title: "Asset standardization",
    description: "Asset conforms to issuance standards locally and globally"
  }
];

const AssetEligibilityPage = ({ asset, history }) => {
  const [formState, updateFormState] = useReducer(
    (state, updator) => updator(state),
    pageData
  );

  const handleCancelClick = () => {
    history.goBack();
  };

  const handleEligibilityChange = (value, title) => {
    return updateFormState(prevState =>
      prevState.map(item => (item.title === title ? { ...item, value } : item))
    );
  };

  if (!asset) return <div />;

  const isEligible = every(formState.map(({ value }) => value));

  const handleNextClick = () => {
    if (isEligible) {
      history.push(`/asset/${asset.name}/technical-assessment`);
    } else {
      history.push(`/asset/${asset.name}/confirm-eligibility`);
    }
  };

  return (
    <Container maxWidth="lg" modifiers={["mt30"]}>
      <PageHeader
        header="Review Asset Eligibility"
        assetName={asset.name}
        handleCancelClick={handleCancelClick}
        handleNextClick={handleNextClick}
      />
      <Box>
        {formState.map(item => (
          <EligibilityItem
            key={item.title}
            value={true}
            handleChange={handleEligibilityChange}
            {...item}
          />
        ))}
      </Box>
      <Box display="flex" justifyContent="flex-end" modifiers={["m30"]}>
        <div>
          <Text modifiers={["rightAlign"]}>Asset Eligibility</Text>
          <Text
            modifiers={["bold", "rightAlign"]}
            color={isEligible ? "primary" : "error"}
          >
            {!isEligible && "Not "} Eligible for technical assessment
          </Text>
        </div>
      </Box>
    </Container>
  );
};

export default withAssets(AssetEligibilityPage);
