import { SET_USER, REMOVE_USER } from "../actions/actionTypes";

const userReducer = (state = {}, action = {}) => {
  switch (action.type) {
    case SET_USER: {
      return { ...action.data, ...state };
    }
    case REMOVE_USER: {
      return {};
    }
    default:
      return state;
  }
};

export default userReducer;
