import React from "react";
import { connect } from "react-redux";
import { Button, Box, Text, Link } from "../components/elements";

const PageHeader = ({
  isMobile,
  header,
  assetName,
  handleCancelClick,
  handleNextClick,
  backBtnText = "Back",
  nextBtnText = "Next"
}) => {
  return (
    <Box
      display={isMobile ? "block" : "flex"}
      justifyContent="space-between"
      modifiers="mb30"
    >
      <div>
        <Text variant="h3" modifiers={["bold"]}>
          {header}
        </Text>
        <Link
          to={`/asset/${assetName}/eligibility`}
          target="_blank"
          onClick={event => {
            event.preventDefault();
            window.open(`http://localhost:3000/asset/${assetName}?popout`);
          }}
        >
          <Text variant="subtitle1" modifiers="mt10">
            View asset summary
          </Text>
        </Link>
      </div>
      <Box display="flex" modifiers={isMobile ? "mt10" : []}>
        <Button
          onClick={handleCancelClick}
          variant="contained"
          modifiers={["mr10", "height"]}
          height={36}
        >
          {backBtnText}
        </Button>
        <Button
          onClick={handleNextClick}
          color="primary"
          variant="contained"
          height={36}
          modifiers="height"
        >
          {nextBtnText}
        </Button>
      </Box>
    </Box>
  );
};

export default connect(state => ({ isMobile: state.default.isMobile }))(
  PageHeader
);
