import React from "react";

import { Box, Text, Table, Tbody } from "../../components/elements";
import TechAssessmentField from "./TechAssessmentField";

const titleWidth = 380;

const TechAssessmentSection = ({ sectionTitle, fields, updateParentState }) => {
  return (
    <Box modifiers={["mb20", "pb10", "bottomBorder"]}>
      <Text variant="h5">{sectionTitle}</Text>
      <Table modifiers={["noBorder", "tightPadding"]}>
        <Tbody>
          {fields.map(field => (
            <TechAssessmentField
              key={field.title}
              titleWidth={titleWidth}
              updateParentState={updateParentState}
              {...field}
            />
          ))}
        </Tbody>
      </Table>
    </Box>
  );
};

export default TechAssessmentSection;
