import React from "react";
import { connect } from "react-redux";
import { AppBar, Toolbar } from "@material-ui/core";
import { Link, Text, Button } from "./elements";
import _isEmpty from "lodash/isEmpty";
import client from "../utils/client";
import { removeUser } from "../store/actions";

const Navbar = ({ user, removeUser, isMobile }) => {
  const brandURL = "/";
  const userRole = user.role === "ANALYST" ? "- Analyst" : "";

  const logout = () => {
    client.get("/logout");
    removeUser();
  };

  const flexStyles = { display: "flex", justifyContent: "space-between" };

  return (
    <AppBar position="static">
      <Toolbar position="static" style={flexStyles}>
        <Link to={brandURL} modifiers="noLinkStyles">
          <Text variant="h6">
            {isMobile
              ? "DASRI"
              : `MIS Digital Asset Ratings Dashboard ${userRole}`}
          </Text>
        </Link>
        {_isEmpty(user) ? (
          <Link to="/login" modifiers="noLinkStyles">
            <Button color="inherit">Login</Button>
          </Link>
        ) : (
          <Button color="inherit" onClick={logout}>
            Logout
          </Button>
        )}
      </Toolbar>
    </AppBar>
  );
};

const mapStateToProps = state => ({
  user: state.user,
  isMobile: state.default.isMobile
});

const mapDispatchToProps = dispatch => ({
  removeUser: () => dispatch(removeUser())
});

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
