import React, { useState } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import { TablePagination, TableSortLabel } from "@material-ui/core";
import filter from "lodash/filter";
import includes from "lodash/includes";
import MobileAssetTable from "./MobileAssetTable";
import Search from "../../components/SearchInput";
import { Box, Table, Tbody, Td, Th, Tr } from "../../components/elements";

function simpleSort(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) return -1;
  if (b[orderBy] > a[orderBy]) return 1;
  return 0;
}

function getOrderingMethod(order, orderBy) {
  return order === "desc"
    ? (a, b) => simpleSort(a, b, orderBy)
    : (a, b) => -simpleSort(a, b, orderBy);
}

function sortData(data, orderingMethod) {
  const stabilizedThis = data.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = orderingMethod(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });

  return stabilizedThis.map(el => el[0]);
}

function filterAssets(assets, searchTerm) {
  if (assets.length === 0 || !searchTerm) return assets;
  return filter(assets, item => {
    if (includes(item.name.toLowerCase(), searchTerm.toLowerCase()))
      return true;
    // if (includes(item.issuance.toLowerCase(), searchTerm.toLowerCase()))
    //   return true;
    if (includes(item.reviewType.toLowerCase(), searchTerm.toLowerCase()))
      return true;

    return false;
  });
}

const headers = [
  {
    id: "name",
    label: "Name of Issuer"
  },
  { id: "issuance", label: "Issuance" },
  { id: "reviewType", label: "Type" },
  { id: "dateSubmitted", label: "Submitted", alignRight: true }
];

const SortIcon = styled.span`
  border: 0;
  clip: rect(0 0 0 0);
  height: 1;
  margin: -1;
  overflow: hidden;
  padding: 0;
  position: absolute;
  top: 20;
  width: 1;
`;

const AssetTable = ({ assets, history, isMobile }) => {
  const handleSortClick = property => {
    const isDesc = orderBy === property && order === "desc";
    setOrder(isDesc ? "asc" : "desc");
    setOrderBy(property);
  };
  const handleRowClick = name => history.push(`/asset/${name}`);
  const handleChangePage = (_, newPage) => setPage(newPage);
  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  const handleSearch = searchTerm => setSearch(searchTerm);

  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("name");
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [search, setSearch] = useState("");
  const emptyRows =
    rowsPerPage - Math.min(rowsPerPage, assets.length - page * rowsPerPage);

  const sortedData = sortData(
    filterAssets(assets, search),
    getOrderingMethod(order, orderBy)
  ).slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  if (isMobile) return <MobileAssetTable data={sortedData} history={history} />;

  return (
    <Box modifiers={["mt30"]}>
      <Box display="flex" justifyContent="flex-end" modifiers="p10">
        <Search value={search} onChange={e => handleSearch(e.target.value)} />
      </Box>
      <Table style={{ minWidth: 750 }} size="medium">
        <Th>
          <Tr>
            {headers.map(({ id, label, alignRight }) => (
              <Td
                key={id}
                align={alignRight ? "right" : "left"}
                sortDirection={orderBy === id ? order : false}
              >
                <TableSortLabel
                  active={orderBy === id}
                  direction={order}
                  onClick={() => handleSortClick(id)}
                >
                  {label}
                  {orderBy === id && (
                    <SortIcon>
                      {order === "desc"
                        ? "sorted descending"
                        : "sorted ascending"}
                    </SortIcon>
                  )}
                </TableSortLabel>
              </Td>
            ))}
          </Tr>
        </Th>
        <Tbody>
          {sortedData.map(({ issuerName, name, reviewType, dateCreated }) => (
            <Tr
              hover
              onClick={() => handleRowClick(name)}
              key={name}
              modifiers="cursor"
            >
              <Td>{issuerName}</Td>
              <Td>{name}</Td>
              <Td>{reviewType}</Td>
              <Td align="right">{dateCreated}</Td>
            </Tr>
          ))}
          {emptyRows > 0 && (
            <Tr style={{ height: 53 * emptyRows }}>
              <Td colSpan={6} />
            </Tr>
          )}
        </Tbody>
      </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={assets.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </Box>
  );
};

export default connect(({ default: { isMobile } }) => ({ isMobile }))(
  AssetTable
);
