import React, { useState } from "react";
import { connect } from "react-redux";
import { Box, Text, Button } from "../../components/elements";
import BorderColorIcon from "@material-ui/icons/BorderColor";
import { Radio, RadioGroup, FormControlLabel } from "@material-ui/core";
import { ToggleButtonGroup, ToggleButton } from "@material-ui/lab";

const MobileToggle = ({ value, _handleConfirm }) => (
  <Box modifiers={["mt20", "ml5"]}>
    <ToggleButtonGroup
      value={value}
      exclusive
      onChange={(e, value) => _handleConfirm(value, true)}
    >
      <ToggleButton value={true}>Yes</ToggleButton>
      <ToggleButton value={false}>No</ToggleButton>
    </ToggleButtonGroup>
  </Box>
);

const StandardToggle = ({
  locked,
  handleEditClick,
  value,
  internalValue,
  _handleChange,
  disableConfirmBtn,
  _handleConfirm,
  setLocked
}) => {
  if (locked)
    return (
      <Box display="flex">
        <Text modifiers={["bold", "mr20"]}>{value ? "YES" : "NO"}</Text>
        <BorderColorIcon
          style={{
            marginTop: "4px",
            fontSize: "1rem",
            cursor: "pointer"
          }}
          onClick={handleEditClick}
        />
      </Box>
    );

  return (
    <RadioGroup value={internalValue} onChange={_handleChange}>
      <Box display="flex">
        <FormControlLabel value={true} control={<Radio />} label="Yes" />
        <FormControlLabel value={false} control={<Radio />} label="No" />
        <Button
          size="small"
          variant="contained"
          modifiers="mr5"
          onClick={() => setLocked(true)}
        >
          Cancel
        </Button>
        <Button
          size="small"
          variant="contained"
          color="primary"
          disabled={disableConfirmBtn}
          onClick={_handleConfirm}
        >
          Confirm
        </Button>
      </Box>
    </RadioGroup>
  );
};

const EligibilityItem = ({
  title,
  description,
  handleChange,
  value,
  isMobile
}) => {
  const [internalValue, setInternalValue] = useState(value);
  const [locked, setLocked] = useState(true);
  const [disableConfirmBtn, setDisableConfirmBtn] = useState(true);

  const handleEditClick = () => {
    setInternalValue(value);
    setLocked(false);
  };

  const _handleChange = e => {
    setInternalValue(e.target.value === "true");
    setDisableConfirmBtn(false);
  };

  const _handleConfirm = (eventValue, useEvent) => {
    handleChange(useEvent ? eventValue : internalValue, title);
    setLocked(true);
  };

  return (
    <Box
      display="flex"
      justifyContent="space-between"
      modifiers={["mb10", "p20", "bottomBorder"]}
    >
      <div>
        <Text modifiers="bold">{title}</Text>
        <Text>{description}</Text>
      </div>
      {isMobile ? (
        <MobileToggle value={value} _handleConfirm={_handleConfirm} />
      ) : (
        <StandardToggle
          locked={locked}
          setLocked={setLocked}
          handleEditClick={handleEditClick}
          value={value}
          internalValue={internalValue}
          _handleChange={_handleChange}
          disableConfirmBtn={disableConfirmBtn}
          _handleConfirm={_handleConfirm}
        />
      )}
    </Box>
  );
};

export default connect(({ default: { isMobile } }) => ({ isMobile }))(
  EligibilityItem
);
