import React, { useState } from "react";
import sum from "lodash/sum";
import values from "lodash/values";
import withAssets from "../../utils/withAssets";
import PageHeader from "../../components/PageHeader";
import { Container, Box, Text } from "../../components/elements";
import techAssessmentModel from "./techAssessmentModel";
import TechAssessmentSection from "./TechAssessmentSection";

const initialState = techAssessmentModel.reduce((acc, section) => {
  const reducedSection = section.fields.reduce((accc, { title }) => {
    accc[title] = 0;
    return accc;
  }, {});

  return {
    ...reducedSection,
    ...acc
  };
}, {});

const TechnicalAssessmentPage = ({ asset, history }) => {
  const [scores, setScores] = useState(initialState);

  const handleNextClick = () => {
    console.log("next");
  };

  const handleCancelClick = () => {
    history.goBack();
  };

  const updateAssessmentState = option => {
    const { score, title } = option;

    setScores(prevState => ({
      ...prevState,
      [title]: score
    }));
  };

  const totalScore = sum(values(scores));

  if (!asset) return <div />;

  return (
    <Container maxWidth="lg" modifiers={["mt30"]}>
      <PageHeader
        header="Technical Assessment"
        assetName={asset.name}
        handleCancelClick={handleCancelClick}
        handleNextClick={handleNextClick}
      />
      {techAssessmentModel.map(section => (
        <TechAssessmentSection
          key={section.sectionTitle}
          updateParentState={updateAssessmentState}
          {...section}
        />      
      ))}
      <Box modifiers={["pt20", "pb20"]}>
        <Box display="flex" justifyContent="flex-end">
          <Text width={200} modifiers="width">
            Total Score:
          </Text>
          <Text width={100} modifiers="width">
            {totalScore}/920
          </Text>
        </Box>
        <Box display="flex" justifyContent="flex-end">
          <Text width={200} modifiers={["bold", "width"]}>
            Weighted Score:
          </Text>
          <Text width={100} modifiers={["bold", "width"]}>
            4.02
          </Text>
        </Box>
      </Box>
    </Container>
  );
};

export default withAssets(TechnicalAssessmentPage);
