import { SET_ASSETS, UPDATE_ASSET } from "../actions/actionTypes";

const assetReducer = (state = [], action = {}) => {
  switch (action.type) {
    case SET_ASSETS: {
      return action.data;
    }
    case UPDATE_ASSET: {
      return state.map(asset =>
        asset.id === action.data.id ? { ...asset, ...action.data } : asset
      );
    }
    default:
      return state;
  }
};

export default assetReducer;
