import React, { useState } from "react";
import find from "lodash/find";
import { FormControl, MenuItem, InputLabel } from "@material-ui/core";
import { Text, Select, Tr, Td } from "../../components/elements";

const TechAssessmentField = ({
  title,
  titleWidth,
  // description,
  options,
  importanceMultiplier,
  updateParentState
}) => {
  const inputLabel = React.useRef(null);
  const [labelWidth, setLabelWidth] = useState(0);
  const [chosenOption, setChosenOption] = useState({ score: [], text: "" });
  let [ratingText, ratingScore] = chosenOption.score;

  if (ratingScore) {
    ratingScore = importanceMultiplier * ratingScore;
  }

  const handleChange = e => {
    const textVal = e.target.value;
    const pickedOption = find(options, opt => opt.text === textVal);
    const [_, score] = pickedOption.score;
    
    setChosenOption(pickedOption);
    updateParentState({
      title,
      score: score * importanceMultiplier
    });
  };

  return (
    <Tr key={title}>
      <Td modifiers="width" width={titleWidth}>
        <Text modifiers="bold">{title}</Text>
        {/* <Text modifiers={["width", "small", "mt5"]} width={titleWidth - 50}>
          {description}
        </Text> */}
      </Td>
      <Td modifiers={["pl20", "pr20", "width"]} width={600}>
        <FormControl variant="outlined" style={{ width: "100%" }}>
          <InputLabel ref={inputLabel} id={`label-id-${title[0]}`}>
            Choose an option
          </InputLabel>
          <Select
            variant="outlined"
            value={chosenOption.text}
            onChange={handleChange}
            labelId={`label-id-${title[0]}`}
            labelWidth={labelWidth}
            modifiers={["fullWidth"]}
            onClick={() => setLabelWidth(130)}
          >
            {options.map(option => (
              <MenuItem key={option.text} value={option.text}>
                {option.text}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Td>
      <Td modifiers="width" width={80}>
        <Text align="center">{ratingText || "-"}</Text>
      </Td>
      <Td modifiers="width" width={40}>
        <Text align="center">{ratingScore || "-"}</Text>
      </Td>
    </Tr>
  );
};

export default TechAssessmentField;
