import React, { useState } from "react";
import MenuItem from "@material-ui/core/MenuItem";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";
import PageHeader from "../../components/PageHeader";
import {
  Container,
  Text,
  Box,
  Input,
  Table,
  Tbody,
  Tr,
  Td
} from "../../components/elements";
import withAssets from "../../utils/withAssets";

const ratings = [
  "AAA",
  "AA1",
  "AA2",
  "AA3",
  "A1",
  "A2",
  "A3",
  "BAA1",
  "BAA2",
  "BAA3",
  "BA1",
  "BA2",
  "BA3",
  "B1",
  "B2",
  "B3",
  "CAA1",
  "CAA2",
  "CAA3",
  "CA",
  "C"
];

const CreditRatingPage = ({ asset, history }) => {
  const [creditRating, setCreditRating] = useState({
    error: false,
    value: ""
  });
  const [ratingDate, setRatingDate] = useState(new Date());
  const [notes, setNotes] = useState("");

  const handleNextClick = () => {
    if (creditRating.value === "") {
      setCreditRating({ error: true, value: "" });
      return;
    }

    // DO SOMETHING HERE  -- SAVE RATING TO DB?

    history.push(`/asset/${asset.name}/eligibility`);
  };

  const handleCancelClick = () => {
    history.goBack();
  };

  if (!asset) return <div />;

  return (
    <Container maxWidth="lg" modifiers={["mt30"]}>
      <PageHeader
        header="Credit Rating"
        assetName={asset.name}
        handleCancelClick={handleCancelClick}
        handleNextClick={handleNextClick}
      />

      <Box style={{ maxWidth: 600 }}>
        <Text gutterBottom>
          Lorem ipsum scaling sit blockchain, decentralized economy elit, sed do
          replace tempor monetary ut labore et dollare magna distributed. Ut
          smart contracts ad minim transaction fees, quis notarization
          masternode proof-of-laboris profit nisi ut governance ea investment
          consequat
        </Text>
        <Table>
          <Tbody>
            <Tr>
              <Td style={{ width: 200 }} modifiers="noBorder">
                <Text modifiers="bold">Credit Rating</Text>
              </Td>
              <Td style={{ width: 400 }} modifiers="noBorder">
                {creditRating.error && (
                  <Text color="error">Please enter a credit rating</Text>
                )}
                <Input
                  select
                  variant="outlined"
                  modifiers="fullWidth"
                  value={creditRating.value}
                  onChange={e =>
                    setCreditRating({ error: false, value: e.target.value })
                  }
                >
                  {ratings.map(rating => (
                    <MenuItem key={rating} value={rating}>
                      {rating}
                    </MenuItem>
                  ))}
                </Input>
              </Td>
            </Tr>
            <Tr>
              <Td style={{ width: 200 }} modifiers="noBorder">
                <Text modifiers="bold">Rating Date</Text>
              </Td>
              <Td style={{ width: 400 }} modifiers="noBorder">
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    style={{ width: "100%" }}
                    variant="inline"
                    margin="normal"
                    label="Choose a date"
                    format="MM/dd/yyyy"
                    value={ratingDate}
                    onChange={setRatingDate}
                  />
                </MuiPickersUtilsProvider>
              </Td>
            </Tr>
            <Tr>
              <Td style={{ width: 200 }} modifiers="noBorder">
                <Text modifiers="bold">Notes</Text>
              </Td>
              <Td style={{ width: 400 }} modifiers="noBorder">
                <Input
                  multiline
                  rows={6}
                  variant="outlined"
                  modifiers="fullWidth"
                  value={notes}
                  onChange={e => setNotes(e.target.value)}
                />
              </Td>
            </Tr>
          </Tbody>
        </Table>
      </Box>
    </Container>
  );
};

export default withAssets(CreditRatingPage);
