import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import PrivateRoute from "./components/PrivateRoute";
import Login from "./pages/Login";
import Dashboard from "./pages/Dashboard";
import AssetReviewPage from "./pages/AssetReview";
import CreditRatingPage from "./pages/CreditRating";
import AssetEligibilityPage from "./pages/AssetEligibility";
import TechnicalAssessmentPage from "./pages/TechnicalAssessment";
import ConfirmEligibility from "./pages/ConfirmEligibility";

const Routes = () => (
  <Switch>
    <Route exact path="/login" component={Login} />
    <PrivateRoute exact path="/dashboard" component={Dashboard} />
    <PrivateRoute
      exact
      path="/asset/:name"
      render={props => <AssetReviewPage {...props} />}
    />
    <PrivateRoute
      exact
      path="/asset/:name/credit-rating"
      render={props => <CreditRatingPage {...props} />}
    />
    <PrivateRoute
      exact
      path="/asset/:name/eligibility"
      render={props => <AssetEligibilityPage {...props} />}
    />
    <PrivateRoute
      exact
      path="/asset/:name/confirm-eligibility"
      render={props => <ConfirmEligibility   {...props} />}
    />
    <PrivateRoute
      exact
      path="/asset/:name/technical-assessment"
      render={props => <TechnicalAssessmentPage {...props} />}
    />
    <Redirect exact from="/*" to="/dashboard" />
  </Switch>
);

export default Routes;
