import React from "react";
import styled from "styled-components";
import moment from "moment";
import numeral from "numeral";
import includes from "lodash/includes";
import {
  Container,
  Text,
  Button,
  Box,
  Table,
  Tbody,
  Tr,
  Td
} from "../../components/elements";
import withAssets from "../../utils/withAssets";

numeral.register("locale", "us", {
  delimiters: {
    thousands: " ",
    decimal: "."
  },
  abbreviations: {
    thousand: "L",
    million: "M",
    billion: "B",
    trillion: "T"
  },
  ordinal: function(number) {
    return number === 1 ? "er" : "me";
  },
  currency: {
    symbol: "$"
  }
});
numeral.locale("us");

const DocumentList = ({ docs }) => {
  const Styles = styled.div`
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }
  `;
  return (
    <Styles>
      <ul>
        {docs.map(doc => (
          <li key={doc.documentTitle}>
            <a target="_blank" href={doc.documentUrl}>
              <Text modifiers="m10">{doc.documentTitle}</Text>
            </a>
          </li>
        ))}
      </ul>
    </Styles>
  );
};

const AssetReviewPage = ({ asset, history, isMobile }) => {
  const handleReviewClick = () => {
    const routeTo = includes(asset.reviewType, "Credit Rating")
      ? "credit-rating"
      : "eligibility";
    history.push(`${history.location.pathname}/${routeTo}`);
  };

  if (!asset) return <div />;

  const {
    name,
    issuerName,
    keyLineOfBusiness,
    address,
    foundingDate,
    issuanceDate,
    assetCategory,
    reviewType,
    settlementCurrency,
    faceValue,
    couponAmount,
    interestPaymentFrequency,
    financialDocuments = [],
    networkOption,
    analyticsProvider,
    dataStorageProvider,
    legalDocuments = [],
    codeAudits = [{}],
    codeTemplate,
    custodian,
    thirdPartyServiceProvider
  } = asset;

  var currency_symbols = {
    USD: "$", // US Dollar
    EUR: "", // Euro
    CRC: "", // Costa Rican Coln
    GBP: "", // British Pound Sterling
    ILS: "", // Israeli New Sheqel
    INR: "", // Indian Rupee
    JPY: "", // Japanese Yen
    KRW: "", // South Korean Won
    NGN: "", // Nigerian Naira
    PHP: "", // Philippine Peso
    PLN: "z", // Polish Zloty
    PYG: "", // Paraguayan Guarani
    THB: "", // Thai Baht
    UAH: "", // Ukrainian Hryvnia
    VND: "" // Vietnamese Dong
  };

  const currency = currency_symbols[settlementCurrency];

  const pageData = [
    {
      sectionTitle: "Background",
      data: [
        { title: "Issue name", value: name },
        { title: "Issuer name", value: issuerName },
        { title: "Description", value: keyLineOfBusiness },
        { title: "Headquarters", value: address },
        {
          title: "Founding date",
          value: moment(foundingDate).format("MMMM YYYY")
        },
        {
          title: "Issuance Date",
          value: moment(issuanceDate).format("MM/DD/YYYY")
        },
        { title: "Asset Category", value: assetCategory },
        { title: "Scenario", value: reviewType }
      ]
    },
    {
      sectionTitle: "Credit rating or financial information",
      data: [
        { title: "Credit Rating", value: "-" },
        { title: "Rating Date", value: "-" },
        {
          title: "Face Value",
          value: `${currency}${numeral(faceValue).format("0.0a")}`
        },
        {
          title: "Maturity Date",
          value: "[COMPUTE]"
        },
        { title: "Coupon", value: `${couponAmount}%` },
        {
          title: "Interest Payment date",
          value: interestPaymentFrequency
        },
        {
          title: "Financial Information",
          value: <DocumentList docs={financialDocuments} />
        }
      ]
    },
    {
      sectionTitle: "Platform",
      data: [
        { title: "Network option", value: networkOption },
        { title: "Analytics provider", value: analyticsProvider },
        { title: "Data storage provider", value: dataStorageProvider }
      ]
    },
    {
      sectionTitle: "Relevant legal & technical information",
      data: [
        { title: "Auditor", value: codeAudits[0].conductedBy },
        {
          title: "Audit Report",
          value: (
            <a target="_blank" href={codeAudits[0].auditReport}>
              <Text modifiers="m10">
                {isMobile ? "auditReport.pdf" : codeAudits[0].auditReport}
              </Text>
            </a>
          )
        },
        { title: "Code Repository", value: "[NEED INFO]" },
        { title: "Code Template", value: codeTemplate },
        { title: "Custodian", value: custodian },
        {
          title: "Legal Documents",
          value: <DocumentList docs={legalDocuments} />
        },
        {
          title: "Other 3rd party provider",
          value: thirdPartyServiceProvider
        }
      ]
    },
    {
      sectionTitle: "Smart Contract",
      data: [
        { title: "Technical Documentation", value: "[NEED INFO]" },
        { title: "Insurance Certificate", value: "[NEED INFO]" },
        { title: "Bug Bounty", value: "[NEED INFO]" },
        { title: "Data Provider", value: "Truset" }
      ]
    }
  ];

  const showStartBtn = history.location.search !== "?popout";

  return (
    <Container maxWidth="lg" modifiers={["mt30"]}>
      <Box display="flex" justifyContent="space-between" modifiers="mb30">
        <Text variant={isMobile ? "h5" : "h3"} modifiers={["bold"]}>
          Request Summary
        </Text>
        {showStartBtn && (
          <Button
            onClick={handleReviewClick}
            color="primary"
            variant="contained"
            height={isMobile && "40px"}
            modifiers="height"
          >
            {isMobile ? "Begin" : "Begin Review"}
          </Button>
        )}
      </Box>
      {pageData.map(section => (
        <Box key={section.sectionTitle} modifiers="mb20">
          <Text modifiers="mt30" variant="h5">
            {section.sectionTitle}
          </Text>
          <Table
            style={{ width: isMobile ? "100%" : 750 }}
            modifiers="tightPadding"
          >
            <Tbody>
              {section.data.map(({ title, value }) => (
                <Tr key={title}>
                  <Td
                    style={{ width: !isMobile && 250 }}
                    modifiers={["noBorder"]}
                  >
                    <Text modifiers={["bold", "m10"]}>{title}</Text>
                  </Td>
                  <Td
                    modifiers={["noBorder"]}
                    style={{ width: !isMobile && 500 }}
                  >
                    {typeof value === "string" || !value ? (
                      <Text modifiers="m10">{value || "-"}</Text>
                    ) : (
                      value
                    )}
                  </Td>
                </Tr>
              ))}
            </Tbody>
          </Table>
        </Box>
      ))}
    </Container>
  );
};

export default withAssets(AssetReviewPage);
