import React, { useEffect, FunctionComponent } from "react";
import { Dispatch, AnyAction } from 'redux';
import StateInterface from "../../store/interface";
import isEmpty from "lodash/isEmpty";
import { connect } from "react-redux";
import { Grid } from "@material-ui/core";
import { fetchAssets } from "../../store/actions";
import { Container, Text } from "../../components/elements";
import AssetTable from "./AssetTable";
import { RouteComponentProps } from "react-router";

interface DashboardInterface {
  fetchAssets: (q?: string) => any,
  fetchUser: () => any,
  updateMobileFlag: (event: Event) => any,
  assets: object[]
}

const Dashboard: FunctionComponent<DashboardInterface & RouteComponentProps> = ({
  assets,
  fetchAssets,
  history
}) => {
  useEffect(() => {
    const fetchThem = async () => fetchAssets();
    if (isEmpty(assets)) fetchThem();
  }, [assets, fetchAssets]);

  return (
    <Container maxWidth="lg" modifiers={["mt30"]}>
      <Grid container>
        <Grid item sm>
          <Text variant="h3" modifiers={["bold"]}>
            Dashboard
          </Text>
        </Grid>
      </Grid>
      <AssetTable assets={assets} history={history} />
    </Container>
  );
};

const mapDispatchToProps = (dispatch: Dispatch<AnyAction, StateInterface>) => ({
  fetchAssets: (q?: string) => dispatch(fetchAssets(q))
});

const mapStateToProps = (state: StateInterface) => ({
  assets: state.assets
});

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
