import React, { useEffect, FunctionComponent } from "react";
import { Dispatch, AnyAction } from 'redux';
import { connect } from "react-redux";
import { BrowserRouter } from "react-router-dom";
import { ThemeProvider } from "@material-ui/styles";
import theme from "./styles/theme";
import Navbar from "./components/Navbar";
import { Box } from "./components/elements"
import Routes from "./Routes";
import StateInterface from "./store/interface"
import { fetchUser, updateMobileFlag } from "./store/actions";


interface AppInterface {
  isMobile: boolean | null,
  fetchUser: () => any,
  updateMobileFlag: (event: Event) => any
}


const App: FunctionComponent<AppInterface> = ({ fetchUser, updateMobileFlag, isMobile }) => {
  useEffect(() => {
    window.addEventListener("resize", updateMobileFlag);
    if (isMobile === null) {
      // initial page load
      window.dispatchEvent(new Event('resize'));
    }
    return () => window.removeEventListener("resize", updateMobileFlag);
  }, [updateMobileFlag, isMobile]);

  useEffect(() => {
    (async () => fetchUser())();
  }, [fetchUser]);

  return (

    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <Navbar />
        <Box modifiers="mb30">
          <Routes />
        </Box>
      </ThemeProvider>
    </BrowserRouter>
  );
};

const mapStateToProps = (state: StateInterface) => ({
  isMobile: state.default.isMobile
});

const mapDispatchToProps = (dispatch: Dispatch<AnyAction, StateInterface>) => ({
  fetchUser: () => dispatch(fetchUser()),
  updateMobileFlag: (event: Event) => dispatch(updateMobileFlag(event))
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
